export default {
	data() {
		return {
			news:[]
		}
	},
	onLoad() {
		// 请求数据
		uni.request({
			url: 'http://unidemo.dcloud.net.cn/api/news',
			method: 'GET',
			data: {},
			success: res => {
				this.news = res.data;
				
			},
			fail: () => {},
			complete: () => {}
		});

	},
	methods: {
		openinfo(e){
			var newsid = e.currentTarget.dataset.newsid;
			uni.navigateTo({
				url: '../info/info?newsid='+newsid,
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		}

	}
}
